  font-family: "playfair_displayregular";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "poppinsbold";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format("woff2");
  font-weight: normal;
  font-style: normal;
}

:root {
  --color-primary: #353535;
  --bg-color: rgb(243, 242, 242);
  --font-primary: "playfair_displayregular", serif, sans-serif;
  --font-secondary: "poppinsbold", serif, sans-serif;
  --text-color: #000000;
  --btn-primary-bg: #007bff;
  --btn-primary-color: #ffffff;
  --btn-dark-bg: #343a40;
  --btn-dark-color: #e0e0e0;
  --btn-light-bg: #f8f9fa;
  --btn-light-color: #212529;
}
body {
  background-color: var(--bg-color);
  color: var(--text-color);
  font-family: var(--font-primary), Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
}

h1,
h2,
h3 {
  font-family: var(--font-secondary), Arial, sans-serif;
  color: var(--color-primary);
}

h3 {
  font-size: 1.2rem;
}

header {
  position: relative;
}

header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  clip-path: polygon(0 0, 100% 0, 100% 50%, 0 100%);
  pointer-events: none;
}

header > * {
  position: relative;
  z-index: 1;
}

.grid-header {
  display: grid;
  grid-template-columns: 350px 1fr;
  align-items: center;
  text-align: center;
  padding: 0 30px;
}
img {
  width: 100%;
}
.svg,
svg {
  width: 25px;
}

.dev-container {
  display: grid;
  position: relative;
}

.dev-container img {
  position: relative;
  border-radius: 20px;
  box-shadow: 0px 5px 9px 2px rgb(82 141 211);
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
}

.dev-container img:hover {
  transform: scale(1.1);
}

.fname {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--color-primary);
}
.about-container {
  display: grid;
  grid-template-columns: 1fr;
}
.icons {
  display: grid;
  grid-auto-flow: column;
  justify-self: end;
  gap: 20px;
}
.main-grid {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0 30px 100px;
  margin-top: 50px;
}
.work-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 40px;
  column-gap: 20px;
}
.work-footer {
  background-color: rgb(255, 255, 255);
  padding: 10px 20px;
}
.work-item {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 210px 150px;
  box-shadow: -6px -4px 3px 0px rgb(0 0 0 / 10%);
}

.work-item img {
  transition: transform 0.3s ease-in-out;
}
.work-item img:hover {
  border: 2px dotted #007bff;
  transform: scale(1.08);
}
.work-item p {
  background-color: #3476bd;
}

.work-icon {
  display: grid;
  grid-auto-flow: column;
  gap: 20px;
  justify-self: end;
}

footer {
  background-color: #3476bd;
  color: white;
}

.footer-grid {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  padding: 10px 30px;
}

.contact-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.footer-logo {
  width: 100%;
}

.footer-icons {
  display: flex;
  gap: 20px;
}
.d-grid {
  align-self: flex-start;
}
footer p {
  width: 30ch;
}

.work-name-container {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
}

@media (min-width: 1441px) {
  header,
  .main-grid {
    max-width: 1440px;
    justify-self: center;
  }
  footer {
    width: 1440px;
    justify-self: center;
  }
}

@media (min-width: 700px) and (max-width: 1000px) {
  .work-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 700px) {
  .fname {
    position: absolute;
    bottom: 35px;
    left: 10px;
    color: white;
  }
}

@media (max-width: 700px) {
  .work-wrapper {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 699px) {
  .grid-header {
    grid-template-columns: 1fr;
  }
  header::before {
    bottom: 59%;
  }
  .footer-grid {
    grid-template-columns: 1fr;
    gap: 70px 0;
    padding: 0;
  }
  .contact-container {
    padding: 0 30px;
  }
  .contact-container h3 {
    color: white;
    text-align: center;
  }
  .footer-icons {
    justify-content: center;
  }
  .fname {
    position: absolute;
    color: white;
    bottom: 50px;
    left: 10px;
    font-size: 2em;
    width: 12ch;
  }
  .dev-container img {
    max-width: 70%;
    height: auto;
  }
}
